import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { ReportsComponent } from './reports.component';
import { NgZorroAntModule } from '../../../ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { SalesEmployeeComponent } from './sales-employee/sales-employee.component';
const routes: Routes = [
  {
    path: '',
    component: ReportsComponent
  },
  {
    path: 'sales-category',
    loadChildren: () => import('./sales-menu/sales-menu.module').then(m => m.SalesMenuModule)
  },
  {
    path: 'sales-customer',
    loadChildren: () => import('./sales-customer/sales-customer.module').then(m => m.SalesCustomerModule)
  },
  {
    path: 'sales-service',
    loadChildren: () => import('./sales-service/sales-service.module').then(m => m.SalesServiceModule)
  },
  {
    path: 'sales-employee',
    loadChildren: () => import('./sales-employee/sales-employee.module').then(m => m.SalesEmployeeModule)
  },
  {
    path: 'sales-product',
    loadChildren: () => import('./sales-product/sales-product.module').then(m => m.SalesProductModule)
  },
  {
    path: 'sales-dishes',
    loadChildren: () => import('./sales-dishes/sales-dishes.module').then(m => m.SalesDishesModule)
  },
  {
    path: 'sales-ingredient',
    loadChildren: () => import('./sales-ingredient/sales-ingredient.module').then(m => m.SalesIngredientModule)
  },
  {
    path: 'sales-side-dishes',
    loadChildren: () => import('./sales-side-dishes/sales-side-dishes.module').then(m => m.SalesSideDishesModule)
  },
  {
    path: 'sales-combo',
    loadChildren: () => import('./sales-combo/sales-combo.module').then(m => m.SalesComboModule)
  },
  {
    path: 'sales-table',
    loadChildren: () => import('./sales-table/sales-table.module').then(m => m.SalesTableModule)
  },
  {
    path: 'sales-area',
    loadChildren: () => import('./sales-area/sales-area.module').then(m => m.SalesAreaModule)
  },
  {
    path: 'input-output-inventory',
    loadChildren: () => import('./input-output-inventory/input-output-inventory.module').then(m => m.InputOutputInventoryModule)
  },
  {
    path: 'input-output-return',
    loadChildren: () => import('./input-output-return/input-output-return.module').then(m => m.InputOutputReturnModule)
  },
  {
    path: 'out-of-stock',
    loadChildren: () => import('./out-of-stock/out-of-stock.module').then(m => m.OutOfStockModule)
  },
  {
    path: 'best-selling-dishes',
    loadChildren: () => import('./best-selling-dishes/best-selling-dishes.module').then(m => m.BestSellingDishesModule)
  },
  {
    path: 'best-selling-menu',
    loadChildren: () => import('./best-selling-menu/best-selling-menu.module').then(m => m.BestSellingMenuModule)
  },
  {
    path: 'sales-profit',
    loadChildren: () => import('./sales-profit/sales-profit.module').then(m => m.SalesProfitModule)
  },
  {
    path: 'branches-comparison',
    loadChildren: () => import('./branches-comparison/branches-comparison.module').then(m => m.BranchesComparisonModule)
  },
  {
    path: 'sales-voucher',
    loadChildren: () => import('./sales-voucher/sales-voucher.module').then(m => m.SalesVoucherModule)
  },
  {
    path: 'sales-payment-card',
    loadChildren: () => import('./sales-payment-card/sales-payment-card.module').then(m => m.SalesPaymentCardModule)
  },
]

@NgModule({
  declarations: [ReportsComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class ReportsModule { }
